.pagination {
  &__link {
    &-prev,
    &-next {
      &.disabled {
        @apply text-black-200 cursor-not-allowed;
      }
      svg {
        @apply w-[5px] h-2.5;
      }
    }
    &.active {
      @apply bg-purple-300;
    }
    &:hover:not(.disabled) {
      @apply bg-black-600;
    }
    &:active {
      @apply bg-black-500;
    }
    @apply flex justify-center items-center px-1 rounded-[4px] min-w-[26px] h-[30px] text-12-r-md transition-all duration-300;
  }
  @apply flex justify-center items-center gap-x-[2px];
}