.dropdown {
  &__container {
    &.selected {
      .dropdown__button {
        @apply hidden;
      }
      .dropdown__sort {
        @apply flex;
      }
      @apply flex items-center;
    }
    @apply relative flex flex-col items-end gap-y-1 w-full;
  }
  &__button {
    .button {
      &:hover {
        @apply bg-transparent;
      }
      &:active {
        @apply bg-transparent;
      }
      @apply right-0 absolute p-0 w-2.5 h-[6px];
    }
    svg {
      @apply transition-all duration-300;
    }
    &.active {
      svg {
        @apply rotate-180;
      }
    }
    @apply flex justify-between items-center gap-x-1 w-full cursor-pointer;
  }
  &__inner {
    &.selected {
      @apply inline-flex relative items-center px-2 py-[2px] rounded-[30px];
    }
    @apply flex items-center gap-x-1;
  }
  &__title {
    @apply text-12-r;
  }
  &__list {
    &.active {
      @apply z-50 opacity-100 visible;
    }
    @apply top-[calc(100%+4px)] -z-10 absolute flex flex-col gap-y-[2px] bg-white-000 opacity-0 p-1 border border-black-500 rounded-md w-full min-w-[100px] max-h-[104px] transition-[opacity,z-index,visibility] duration-300 invisible overflow-y-auto;
  }
  &__item {
    input {
      &:checked ~ p {
        @apply bg-black-600 bg-[url(data:image/svg+xml;base64,)];
      }
      @apply top-0 absolute opacity-0 w-0 h-0 invisible;
    }
    p {
      &:hover {
        @apply bg-black-600;
      }
      span {
        &:before {
          @apply left-2 absolute content-[''] rounded-full w-2 h-2;
        }
        @apply inline-flex relative items-center py-[2px] pr-2 pl-5 rounded-[30px];
      }
      @apply block bg-[calc(100%-12px)_center] bg-no-repeat py-[2px] pr-6 pl-2 rounded-[4px] w-full text-12-m-md transition-all duration-300 cursor-pointer;
    }
  }
  &__image {
    @apply mr-[5px] rounded-full max-w-[30px] max-h-[30px] object-cover;
  }
  &__sort {
    .button {
      &:hover,
      &:active {
        @apply bg-transparent;
      }
      @apply top-3 right-[19px] absolute p-0 w-2.5 h-[6px] transition-all duration-300;
    }
    &.active {
      .button {
        @apply rotate-180;
      }
      @apply flex;
    }
    @apply relative hidden p-[5px] pr-10 border border-black-500 rounded-md w-full cursor-pointer;
  }
  &__values {
    @apply flex flex-wrap gap-[5px];
  }
  &__value {
    svg {
      @apply cursor-pointer;
    }
    @apply flex items-center gap-x-2 bg-black-700 px-[6px] py-px line-clamp-1 border border-black-500 rounded-[3px] text-12-m whitespace-nowrap;
  }
  &__label {
    sup {
      @apply ml-px text-red-100;
    }
    @apply text-14-m-p;
  }
  &__items {
    p {
      @apply text-12-m-md;
    }
    .input-checkbox-with-label {
      &:hover {
        @apply bg-black-600;
      }
      &:active {
        @apply bg-black-500;
      }
      @apply px-2 py-[2px] rounded-[4px] transition-all duration-300;
    }
    @apply flex flex-col gap-y-[2px] mt-1 max-h-[276px] overflow-y-auto;
  }
  &-base {
    .dropdown__title {
      @apply text-12-r-p;
    }
  }
  &-checkboxes {
    .dropdown__button {
      .button {
        @apply right-5;
      }
      @apply px-3 py-[7px] border border-black-500 rounded-md w-full;
    }
    .search {
      @apply px-2;
    }
    .checkbox {
      @apply w-full;
    }
    .dropdown__title {
      @apply text-black-200;
    }
    .dropdown__list {
      @apply gap-y-0 bg-white-000 px-2 py-3 w-full max-h-[335px];
    }
  }
  &-with-icon {
    .dropdown__button {
      .button {
        @apply right-[5px];
      }
      @apply gap-x-1 pr-6;
    }
    .dropdown__title {
      @apply text-12-m;
    }
    .dropdown__list {
      @apply top-[calc(100%-2px)] max-w-[145px];
    }
  }
  &-status {
    .dropdown__button {
      .button {
        @apply right-2;
      }
      &:before {
        @apply left-2 absolute content-[''] rounded-full w-2 h-2;
      }
      @apply py-[3px] pr-2 pl-5 rounded-[30px];
    }
  }
  &-priority {
    .dropdown__button {
      .button {
        @apply right-5;
      }
      @apply px-3 py-[7px] pr-5 border border-black-500 rounded-md w-full h-9;
    }
    .dropdown__list {
      @apply py-[9px] w-min min-w-[186px] max-h-[none];
    }
    .dropdown__item {
      p {
        span {
          @apply px-2.5;
        }
        @apply py-[3px];
      }
    }
  }
  .red {
    .dropdown__title {
      @apply font-medium;
    }
    @apply bg-red-200;
  }
  .pink {
    .dropdown__title {
      @apply font-medium;
    }
    @apply bg-pink-000;
  }
  .orange {
    .dropdown__title {
      @apply font-medium;
    }
    @apply bg-orange-000;
  }
  .peach {
    .dropdown__title {
      @apply font-medium;
    }
    @apply bg-peach-000;
  }
  .yellow {
    .dropdown__title {
      @apply font-medium;
    }
    @apply bg-yellow-000;
  }
  .green {
    &:before {
      @apply bg-green-000;
    }
    .dropdown__title {
      @apply font-medium;
    }
    @apply bg-green-000 bg-opacity-30 text-green-000;
  }
  .gray {
    &:before {
      @apply bg-black-200;
    }
    .dropdown__title {
      @apply font-medium;
    }
    @apply bg-black-200 bg-opacity-30 text-black-200;
  }
  .blue {
    &:before {
      @apply bg-blue-200;
    }
    .dropdown__title {
      @apply font-medium;
    }
    @apply bg-blue-200 bg-opacity-30 text-blue-200;
  }
  .purple {
    &:before {
      @apply bg-purple-050;
    }
    .dropdown__title {
      @apply font-medium;
    }
    @apply bg-purple-050 bg-opacity-30 text-purple-050;
  }
  @apply flex flex-col items-start gap-y-2 w-full;
}