.table {
  &-first-td {
    a {
      svg {
        @apply w-3 h-3;
      }
      &:hover {
        @apply text-blue-000;
      }
      &:active {
        @apply text-blue-050;
      }
      @apply flex items-center gap-x-[2px] text-10-r transition-all duration-300;
    }
  }
  &__channels {
    a {
      img {
        @apply rounded-full max-w-[22px] max-h-[22px] object-cover;
      }
      span {
        @apply inline-block line-clamp-1 max-w-[98px] text-12-r-p text-ellipsis;
      }
      &:hover {
        @apply text-blue-000;
      }
      &:active {
        @apply text-blue-050;
      }
      @apply flex items-center gap-x-1 bg-white-000 px-[6px] py-[3px] border border-black-500 rounded-md transition-all duration-300;
    }
    &-toggle {
      &:hover {
        @apply border-blue-500 text-blue-500;
      }
      &:active {
        @apply border-blue-500 text-blue-500;
      }
      svg {
        @apply transition-transform duration-300;
      }
      &.active {
        svg {
          @apply border-blue-100 text-blue-100 rotate-180;
        }
        @apply border-blue-500;
      }
      @apply top-1 right-0 absolute flex justify-center items-center bg-white-000 p-0 border border-black-500 rounded-md w-[26px] h-[22px];
    }
    &.active {
      @apply h-auto;
    }
    @apply relative flex flex-wrap gap-[6px] max-w-[421px] h-[30px] overflow-hidden;
  }
  th {
    p {
      @apply text-12-sb whitespace-nowrap;
    }
    span {
      &.male,
      &.female {
        &:before {
          @apply left-0 absolute content-[''] rounded-full w-2 h-2;
        }
        @apply inline-flex relative items-center pl-2.5 min-w-14 text-black-200;
      }
      &.male {
        &:before {
          @apply bg-blue-200;
        }
      }
      &.female {
        &:before {
          @apply bg-yellow-100;
        }
      }
      @apply text-10-r;
    }
    @apply px-6 border-b border-black-500 h-11 text-left;
  }
  td {
    a {
      &:hover {
        @apply text-blue-000;
      }
      &:active {
        @apply text-blue-050;
      }
      @apply transition-all duration-300;
    }
    span {
      &.male,
      &.female {
        &:before {
          @apply left-0 absolute content-[''] rounded-full w-2 h-2;
        }
        @apply inline-flex relative items-center pl-[14px] min-w-[57px];
      }
      &.male {
        &:before {
          @apply bg-blue-200;
        }
      }
      &.female {
        &:before {
          @apply bg-yellow-100;
        }
      }
    }
    h3 {
      @apply inline-block line-clamp-1 text-12-sb-p text-ellipsis;
    }
    .dropdown {
      &__label {
        @apply font-normal;
      }
      &__button {
        .button {
          svg {
            @apply w-2 h-1;
          }
          @apply relative;
        }
        @apply w-auto;
      }
      &__container {
        @apply inline-flex items-start;
      }
      &__list {
        @apply right-0 w-auto;
      }
      @apply inline-block w-auto;
    }
    &.only-text {
      @apply py-4;
    }
    @apply align-top px-6 py-2 border-b border-black-500 text-12-r-p whitespace-nowrap;
  }
  tbody {
    tr {
      &:hover {
        @apply bg-purple-300;
      }
      @apply transition-all duration-300;
    }
  }
  @apply w-full min-w-[1201px];
}