.button {
  &:hover {
    @apply bg-black-600;
  }
  &:active {
    @apply bg-black-500;
  }
  &:disabled {
    @apply opacity-75 cursor-not-allowed select-none;
  }
  &-confirm {
    &:hover {
      @apply bg-red-050;
    }
    &:active {
      @apply bg-red-000;
    }
    &:disabled {
      @apply opacity-75 cursor-not-allowed select-none;
    }
    @apply bg-red-000 px-12 py-2 rounded-md h-[34px] text-12-m-md text-white-000;
  }
  &-with-icon {
    &:hover {
      @apply bg-black-600;
    }
    &:active {
      @apply bg-purple-300 border-blue-200 text-blue-200;
    }
    &:disabled {
      @apply opacity-75 cursor-not-allowed select-none;
    }
    .button-clear {
      @apply hidden;
    }
    span {
      @apply whitespace-nowrap;
    }
    @apply flex items-center gap-x-1.5 px-2 py-1.5 border border-black-500 rounded-md h-8 text-12-m;
  }
  &-with-icon-blue {
    &:hover {
      @apply bg-black-100;
    }
    &:active {
      @apply bg-black-050;
    }
    &:disabled {
      @apply opacity-75 cursor-not-allowed select-none;
    }
    svg {
      @apply w-3 h-3;
    }
    &.lg {
      @apply px-5 h-[34px];
    }
    @apply flex items-center gap-x-1.5 bg-blue-000 px-2 py-2 rounded-md h-8 text-12-m text-white-000;
  }
  &-with-icon-full {
    &.disabled {
      @apply text-black-200 cursor-not-allowed select-none;
    }
    &:active {
      @apply bg-purple-300 text-blue-200;
    }
    &:hover {
      @apply bg-black-600;
    }
    &:disabled {
      @apply opacity-75 cursor-not-allowed select-none;
    }
    &.active {
      &:active {
        @apply bg-purple-300 text-blue-200;
      }
      @apply bg-purple-300 text-blue-200;
    }
    &.sm {
      @apply gap-x-[6px] px-2 py-[5px] h-7;
    }
    @apply flex items-center gap-x-3 px-3 py-[7px] rounded-md w-full h-8 text-12-m;
  }
  &-icon {
    &:hover {
      @apply bg-black-600;
    }
    &:active {
      @apply bg-black-500;
    }
    &:disabled {
      @apply opacity-75 cursor-not-allowed select-none;
    }
    &.sm {
      @apply w-5 h-5;
    }
    @apply flex justify-center items-center bg-transparent py-[7px] p-1.5 rounded w-[29px] h-[27px];
  }
  &-icon-round {
    &:hover {
      @apply bg-black-100 text-white-000;
    }
    &:active {
      @apply bg-black-050 text-white-000;
    }
    &:disabled {
      @apply opacity-75 cursor-not-allowed select-none;
    }
    svg {
      @apply w-[18px] h-[18px];
    }
    @apply flex justify-center items-center bg-black-700 rounded-full w-12 h-12;
  }
  &-with-icon-full-lg {
    &:hover {
      @apply bg-blue-200;
    }
    &:active {
      @apply bg-blue-000;
    }
    &:disabled {
      @apply bg-blue-200 cursor-not-allowed select-none;
    }
    svg {
      @apply w-4 h-4;
    }
    @apply flex justify-center items-center gap-x-2 bg-blue-000 py-3.5 rounded-lg w-full text-14-m-md text-white-000 tracking-[0.2px];
  }
  &-with-icon-full-google {
    &:hover {
      @apply bg-black-100 text-white-000;
    }
    &:active {
      @apply  bg-black-050 text-white-000;
    }
    &:disabled {
      @apply opacity-75 cursor-not-allowed select-none;
    }
    svg {
      @apply w-4 h-4;
    }
    @apply flex justify-center items-center gap-x-2 bg-black-600 py-3.5 rounded-lg w-full text-14-m-md tracking-[0.2px];
  }
  &-close {
    svg {
      @apply text-black-200 transition-all duration-300;
    }
    &:hover {
      svg {
        @apply text-black-100;
      }
      @apply bg-black-600;
    }
    &:active {
      svg {
        @apply text-black-050;
      }
      @apply bg-black-500;
    }
    &:disabled {
      @apply opacity-75 cursor-not-allowed select-none;
    }
    @apply flex justify-center items-center bg-transparent rounded-full w-[23px] h-[23px];
  }
  &-base {
    @apply bg-white-000 px-5 py-2 border border-black-500 rounded-[4px] h-[34px] text-12-m-md;
  }
  @apply transition-colors duration-300;
}