.login {
  &__image {
    @apply p-11 bg-cover bg-no-repeat bg-center bg-[url(/assets/images/login-bg.png)] h-dvh w-full;
  }
  &__container {
    @apply max-lg:z-10 max-lg:absolute flex justify-center items-center max-lg:bg-white-000 max-lg:mx-2 max-lg:p-4 max-lg:rounded-md;
  }
  &__form {
    @apply mx-auto w-full max-w-[376px];
  }
  &__title {
    @apply mb-[5px] text-30-m-p;
  }
  &__subtitle {
    @apply flex gap-x-1 mb-[26px] text-14-m-p text-black-200;
  }
  &__input {
    &-email {
      @apply mb-3.5;
    }
    &-password {
      @apply mb-2.5;
    }
  }
  &__remember {
    @apply flex justify-between mb-6;
  }
  &__create ,
  &__forgot {
    &:hover,
    &:active {
      @apply text-blue-000;
    }
    @apply text-14-m-p text-blue-200 transition-all duration-300;
  }
  &__divider {
    @apply items-center gap-2 grid grid-cols-[1fr_max-content_1fr] mx-0 my-5;
  }
  &__divider-text {
    @apply text-14-m-p;
  }
  &__line {
    @apply border-b border-black-500 h-px;
  }
  @apply max-lg:flex max-lg:justify-center max-lg:items-center lg:grid lg:grid-cols-[minmax(0,_784px)_minmax(456px,_1fr)] w-full min-h-dvh;
}