.heading {
  &-table {
    &-rows {
      @apply flex-col;
    }
    @apply flex justify-between gap-y-[30px];
  }
  &__buttons {
    @apply flex justify-between items-center gap-x-3;
  }
  &__left,
  &__right {
    @apply flex items-center gap-x-3;
  }
  &__title {
    sup {
      @apply inline-block ml-[6px] text-12-r text-black-200 -translate-y-1;
    }
    @apply flex items-center text-24-sb;
  }
  @apply mt-[30px];
}