.checkbox {
  .button {
    &:hover,
    &:active {
      @apply bg-transparent text-blue-100;
    }
    svg {
      @apply w-[9px] h-[9px];
    }
    @apply p-0 border-none w-auto h-auto;
  }
  @apply flex items-center gap-x-1;
}
.input {
  &__container {
    @apply flex flex-col items-start gap-y-1 w-full;
  }
  &-checkbox {
    input {
      &:checked {
        & ~ span {
          &:before {
            @apply bg-blue-000 border-blue-000;
          }
          &:after {
            @apply opacity-100;
          }
        }
      }
      @apply top-0 absolute opacity-0 w-0 h-0 invisible;
    }
    span {
      &:before {
        @apply absolute content-[''] border border-black-500 rounded-sm w-3 min-w-3 h-3 min-h-3 transition-all duration-300;
      }
      &:after {
        @apply absolute opacity-0 w-3.5 h-3.5 bg-center content-[''] transition-all duration-300 bg-no-repeat bg-[url(data:image/svg+xml;base64,)];
      }
      @apply flex justify-center items-center w-3 min-w-3 h-3 min-h-3;
    }
    p {
      @apply text-12-m-md whitespace-nowrap;
    }
    &:hover {
      span {
        &:before {
          @apply border-blue-000;
        }
      }
    }
    &-with-label {
      @apply flex items-center gap-x-2.5;
    }
    &-with-icon {
      img {
        @apply ml-[6px] rounded-full max-w-[18px] max-h-[18px] object-cover;
      }
      a {
        &:hover {
          @apply text-blue-000;
        }
        &:active {
          @apply text-blue-050;
        }
        @apply inline-block text-10-r text-black-200 transition-all duration-300;
      }
      @apply flex items-center gap-x-1;
    }
    &.lg {
      span {
        &:before {
          @apply w-[18px] min-w-[18px] h-[18px] min-h-[18px];
        }
        &:after {
          @apply scale-125;
        }
        @apply w-[18px] min-w-[18px] h-[18px] min-h-[18px];
      }
      @apply h-[18px] min-h-[18px];
    }
    @apply relative cursor-pointer;
  }
  &-ranges {
    @apply items-center grid grid-cols-2 border border-black-500 rounded-[5px] w-full h-[30px];
  }
  &-range {
    span {
      @apply bg-black-500 px-3 py-[5px] rounded-[4px] text-12-m-md;
    }
    input {
      @apply px-[5px] w-full text-12-m-md;
    }
    @apply flex items-center;
  }
  &-with-label {
    .input__el {
      &.error {
        & ~ .error__text {
          @apply opacity-100 w-auto h-auto text-12-r-p visible;
        }
        @apply bg-red-400 border-red-100;
      }
      &::placeholder {
        @apply visible;
      }
      @apply px-3 py-[11px] h-9 text-12-r-p;
    }
    .error__text {
      @apply flex items-center gap-x-1 opacity-0 w-0 h-0 text-[0] text-red-100 transition-[opacity,visibility] duration-300 invisible;
    }
    @apply flex flex-col items-start gap-y-2;
  }
  &__label {
    sup {
      @apply ml-px text-red-100;
    }
    @apply text-14-m-p;
  }
  &__el {
    &::placeholder {
      @apply invisible;
    }
    &:hover,
    &:focus {
      @apply border-black-100;
    }
    &:focus,
    &:active,
    &.filled {
      & ~ .input__tip {
        @apply -translate-y-[11px];
      }
    }
    &.error {
      @apply border-red-100;
    }
    @apply px-3 pt-[22px] pb-2 border border-black-500 rounded-md w-full h-12 text-14-r transition-all duration-300;
  }
  &__tip {
    @apply left-3 absolute text-12-r text-black-200 transition-all duration-300 pointer-events-none;
  }
  &__toggle {
    svg {
      path {
        @apply transition-all duration-300;
      }
    }
    &.filled {
      .input__eye {
        &-hide {
          @apply hidden;
        }
        @apply block;
      }
    }
    @apply hidden top-1/2 absolute right-3 -translate-y-1/2;
  }
  &__eye {
    &-hide {
      @apply block;
    }
    @apply hidden;
  }
  @apply relative flex items-center w-full;
}
