.header {
  .narrowed {
    @apply hidden;
  }
  &.active {
    .narrowed {
      @apply flex;
    }
    @apply left-[59px] w-[calc(100%-59px)];
  }
  &__wrapper {
    @apply flex justify-between items-center pr-6 w-full;
  }
  &__search {
    @apply flex items-center ml-4 w-full max-w-[243px];
  }
  &__controls {
    @apply flex items-center gap-x-[2px];
  }
  &__dropdown {
    @apply ml-[15px];
  }
  @apply left-[217px] z-50 fixed flex items-center bg-black-700 py-[14px] border-b border-black-500 w-[calc(100%-217px)] h-[60px] transition-all duration-300;
}